.wrapper {
    min-height: 300px;
    background-color: var(--primary);
    color: white;
}
.bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    a {
        text-decoration: none;
        color: white;
        padding: 3px;
    }
}
.logo-contact {
    text-align: center;
    margin-top: 3px;
    a {
        color: white;
        margin: 4px;
    }
}
.middle {
    display: flex;
    justify-content: space-around;
    border-bottom: 1px solid rgb(247, 243, 243);
    padding: 40px 0px;

    a {
        text-decoration: none;
        color: white;
    }
    .private {
        text-align: center;
        margin-bottom: 40px;
    }
    h4 {
        text-align: center;
    }
}
.img-footer {
    height: 40px;
    width: 140px;
}
